function descriptionInfo() {
  const descriptionInfo = $('.description-info');
  descriptionInfo.each(function () {
    const buttons = $(this).find('.description-info__tabs').find('button');
    const blocks = $(this).find('.description-info__info').find('.description-info__block');
    buttons.click(function () {
      buttons.removeClass('active');
      $(this).toggleClass('active');

      const target = $(this).data('target');
      blocks.removeClass('active');
      $(`#${target}`).toggleClass('active');
    });
  });
}

$(document).ready(() => {
  descriptionInfo();
});
